import * as React from "react"
import ProjectItems from "./projectItems"
import ZoeyIcon from "../../images/Zoey.png" 



const ProjectContainer = () => {
    return(
        <containers>
            {ProjectItems.map((item, index) => {
                return(
                    <pcontainer class="container">
                        <header class="grid grid-cols-5 gap-2 transform translate-y-4 translate-x-4">
                            <h2 class=" p-2 bg-gray-800 border-2 border-gray-600 text-center text-2xl text-gray-200 col-span-1">
                                {item.title}
                            </h2>
                            <imgbox class=" text-2xl rounded-lg text-gray-200 col-span-4 flex-row flex space-x-2">
                                <div class="bg-pink-400 transform -skew-x-12 w-24 h-20">
                                    <img src= {ZoeyIcon} alt ="..." class="transform skew-x-12"/>
                                </div>
                                <div class="bg-pink-400 transform -skew-x-12 w-24 h-20 overflow-hidden">
                                    <img src= {ZoeyIcon} alt ="..." class="transform skew-x-12"/>
                                </div>
                                <div class="bg-pink-400 transform -skew-x-12 w-24 h-20 overflow-hidden">
                                    <img src= {ZoeyIcon} alt ="..." class="transform skew-x-12"/>
                                </div>
                                <div class="bg-pink-400 transform -skew-x-12 w-24 h-20 overflow-hidden">
                                    <img src= {ZoeyIcon} alt ="..." class="transform skew-x-12"/>
                                </div>
                                <div class="bg-pink-400 transform -skew-x-12 w-24 h-20 overflow-hidden">
                                    <img src= {ZoeyIcon} alt ="..." class="transform skew-x-12"/>
                                </div>
                                <div class="bg-pink-400 transform -skew-x-12 w-24 h-20 overflow-hidden">
                                    <img src= {ZoeyIcon} alt ="..." class="transform skew-x-12"/>
                                </div>
                                <div class="bg-pink-400 transform -skew-x-12 w-24 h-20 overflow-hidden">
                                    <img src= {ZoeyIcon} alt ="..." class="transform skew-x-12"/>
                                </div>
                            </imgbox>
                        </header>
                        <body class="bg-purple-800 border-2 border-purple-300  rounded-lg p-4 shadow-md"> 
                            {item.description}
                        </body>
                    
                    </pcontainer>
                )
            })}

        </containers>
    )
}

export default ProjectContainer

